.home {

  &__body {
    @include onTablet {
      @include container(30px, 34px);
      @include gridContainer;
    }
  }

  &__image {
    display: flex;
    @include gridItem;
    grid-row: 1 / 2;
    max-height: 245px;

    &::before {
      content: "";
      display: block;
      padding-bottom: 76.6%;
    }

    @include onTablet {
      max-height: 336px;
      margin-left: -8px;
    }

    @include onDesktop {
      z-index: 2;
      max-height: 464px;
      margin-left: -30px;
    }

    & .ibg__img {
      object-fit: contain;
    }

    @include anyHoverImage(transform, 0.9);
  }

  &__contant {
    @include toTablet {
      @include container(30px, 34px);
    }

    @include gridItem;
    grid-row: 1 / 2;
    z-index: 1;

    @include onDesktop {
      padding-top: 80px;
    }
  }

  &__title {
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 40px;
    line-height: 130%;
    text-transform: uppercase;
    color: $colorMain;

    @include onTablet {
      font-size: 48px;
      line-height: 62px;
    }

    @include onDesktop {
      font-size: 62px;
      line-height: 78px;
    }
  }

  &__text {
    font-size: 16px;
    line-height: 150%;
    color: $colorMain;

    @include onDesktop {
      padding-right: 100px;
    }
  }
}
